table {
  border-collapse: collapse;
  width: 100%;
  border-bottom: 1px solid #ddd; 
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);

}

tr:nth-child(even) {
  background-color: #ffffff;
}

tr:hover {
  background-color: #ddd;
}

th,
td {
  padding: 8px;
  text-align: left;
  
}



th {
  background-color: #f7d2d2;
  font-size: 14px;
  font-weight: bold;
}

input[id="unseeinputlist"] {
  width: 20px;
}

details {

  width: 98%;
  margin: 0 auto ;
  background: #f7d2d2;
  margin-bottom: .5rem;
  box-shadow: 0 .1rem 1rem -.5rem rgba(0,0,0,.4);
  border-radius: 10px;
  overflow: hidden;
  padding: 10px;
}

summary {
  padding: 1px;
  display: block;
  background: #f7d2d2;
  padding-left: 2.2rem;
  position: relative;
  cursor: pointer;
}

summary:before {
  content: '';
  border-width: .4rem;
  border-style: solid;
  border-color: transparent transparent transparent #FEB2B2;
  position: absolute;
  top: 0.4rem;
  left: 1rem;
  transform: rotate(0);
  transform-origin: .2rem 50%;
  transition: .25s transform ease;
}

details[open] > summary:before {
  transform: rotate(90deg);
}


details summary::-webkit-details-marker {
  display:none;
}

.submitsure {
  animation-name: submitanimation;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  padding: 7px;
  border-radius: 5px;
  text-align: center;
}

@keyframes submitanimation {
  0% {background-color: #ffffff;}
  50% {background-color: #c3dafa;}
  100% {background-color: #ffffff;}
}


ul {
  list-style: none;
  padding: 5px;
}

li {
  padding: 1px;
}
