  font-family: 'Quicksand', sans-serif;
  color : #000000;
  background-color: #c3dafa;
}


button {
  background-color: #FEB2B2;
  border: none;
  color: white;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  border-radius: 50px;
