  body {
    background-color: #c3dafa;
    /* position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); */
    /* background-image: url("background.jpg"); */
    background-size: cover;
    background-attachment: scroll;
    display: flex;
    align-items: center;
    justify-content: center;



  }


  

  .content {
    position: relative;
    z-index: 1;
  }


  form {
    width: 300px;
  }

  input[name="unseeinputlist"] {
    width: 20px;
  }

  input[type="text"],
  input[type="password"] {
    width: 100%;
    padding: 10px;
    margin-bottom: 20px;
    border-radius: 50px;
    border: none;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    transition: all 0.3s ease;
  }
  
  
  .password-container {
    position: relative;
  }
  
  .password-btn {
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
    background-color: transparent;
    border: none;
    color: #777;
    font-size: 14px;
    cursor: pointer;
  }
  
  .password-btn:hover {
    color: #000;
  }
  
  .submit-btn {
    background-color: #FEB2B2;
    border: none;
    color: white;
    padding: 10px 20px;
    text-align: center;
    text-decoration: none;
    display: block;
    width: 100%;
    font-size: 16px;
    border-radius: 50px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    transition: all 0.3s ease;
    margin-top: 30px;
  }
  
  .submit-btn:hover {
    background-color: #FCA5A5;
    transform: translateY(-2px);
    box-shadow: 0px 8px 8px rgba(0, 0, 0, 0.25);
  }

  .adminpage {
    width: 1000px;
    background-color: aliceblue;
    border-radius: 20px;
    padding: 20px;
  }

